.navbar {
    height: 100%;
    display: grid;
}


.navbar-top {
    background: rgb(63,94,251);
    background: linear-gradient(90deg, rgba(63,94,251,1) 1%, rgba(70,181,252,1) 98%);
    height: 50px;
    z-index: 5;
}

.navbar-menu {
    background: rgb(63,94,251);
    background: linear-gradient(90deg, rgba(63,94,251,1) 1%, rgba(70,181,252,1) 98%);
    transition: all ease-in-out 0.2s;
    position: absolute;
    width: 100%;
    top: 50px;
}
.navbar-menu-active {
    transform: translateY(0);
}
.navbar-menu-hidden {
    transform: translateY(-100%);
}
.navbar-menu li {
    font-size: 2em;
    list-style: none;
    padding: 10px;
    padding-left: 1em;
    color: white;
}
.navbar-menu li a {
    color: white;
}
.navbar-menu li:hover {
    background-color: rgba(255, 255, 255, 0.301);
}

